.jumbotron {
    border: 1px solid black;
}

.jumbotronContent {
    text-align: center;
    line-height: 1.5;
    padding-top: 7.5rem;
    padding-left: 15rem;
    float: left;
    max-width: 35rem;
    width: 30%;
}


.jumbotronImg img {
    max-width: 25%;
    float: right;
    position: relative;
    margin-right: 200px;
    margin-top: 4.0rem;
    box-shadow: 0 0.25rem 0.5rem 0 rgba(0, 0, 0, 0.2), 0 0.375rem 1.25rem 0 rgba(0, 0, 0, 0.19);
}